.cards {
  .container {
    .wrapAllCards {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 10px 10px;
      .wrapCard {
        .wrapImg {
          .imgCard {
            width: 100%;
            object-fit: cover;
            object-position: center;
          }
        }
      }
    }
  }
}
